import React, { useCallback } from 'react';
import debounce from 'lodash.debounce';
import Loader from 'react-loader-spinner';
/*eslint-disable*/

import logo from './logo.png';

import './style.css';

function Auth() {
  const [autoComliteLoading, setAutoComliteLoading] = React.useState(true);
  const [isValidAdress, setIsValidAdress] = React.useState(true);
  const [adress, setAdress] = React.useState('');
  const [list, setList] = React.useState([]);

  const getAutofill = (e:any) => {
    console.log('getAutofill');
    
    const paramsObj = {
      hitsPerPage: '10',
      language: 'ru',

      // query: '%D0%9D%D0%BE%D0%B2%D0%BE%D1%81%D0%B5%D0%BB%D1%8C%D1%81%D0%BA%D0%BE%D0%B3%D0%BE"',
      query: e.target.value,
      //  +  ' Одесса',
      countries: 'ua',
      type: 'address',
    };
    /* const params = Object.keys(paramsObj).map(key => key + '=' + paramsObj[key]).join('&'); */
    const params = 'hitsPerPage=' + paramsObj.hitsPerPage + '&' + 
                    'language=' + paramsObj.language + '&' + 
                    'query=' + paramsObj.query + '&' + 
                    'type=' + paramsObj.type + '&' + 
                    'countries=' + paramsObj.countries;
    // const test = '{"params":"hitsPerPage=5&language=ru&query=%D0%9D%D0%BE%D0%B2%D0%BE%D1%81%D0%B5%D0%BB%D1%8C%D1%81%D0%BA%D0%BE%D0%B3%D0%BE"}';
    // console.log(params);
    // console.log(test);
    const body = JSON.stringify({ params });
    const options = {
      body,
      method: 'POST',
      headers: {
        'Content-Type': 'application/json; charset=utf-8',
        'X-Algolia-Application-Id': 'pl5TUITFNEHW',
        'X-Algolia-API-Key': '02a1caebac8f9f92dc638f89242e443f',
      },
    };
    fetch('https://places-dsn.algolia.net/1/places/query', options).then((r) => { return r.json(); })
    .then((data:any) => {
      console.log(data);
      const res = data.hits.filter((i:any) => {
        if (!i.is_highway) return;
        if (i.city && !["Одесская область", "Одесса"].includes(i.city[0])) return;
        return true;
      });
      setAutoComliteLoading(false);
      console.log(res);
      
      setList(res);
    });
  };
  
  const getAutofillDebounced:any = useCallback(debounce(getAutofill, 400), []);

  const onChange = (e:any) => {
    setAdress(e.target.value);
    setAutoComliteLoading(true);
    setIsValidAdress(false);
    getAutofillDebounced(e);
  };

  const clickHandler = () => {
    localStorage.setItem('test', adress);
    setAutoComliteLoading(true);
  };

  const onClickAutoComlite = (val:string) => {
    setAdress(val);
    setIsValidAdress(true);
  };

  // const showAutoComplite = list.length > 0 || autoComliteLoading;

  return (
    <main className="container Auth">
      <img src={logo} alt="" />

      <span className="town">г. Одесса</span>

      <input 
        type="text"
        value={adress}
        onChange={onChange}
      />

      { adress && !isValidAdress && <div className={'auto_complite'}>
        <div className={'list' + (autoComliteLoading?' mid':'')}>

          { autoComliteLoading && <Loader
              type='Puff'
              color='#00BFFF'
              height={50}
            /> }

          { list.length === 0 && !autoComliteLoading && <div>нет результатов</div> }


          { !autoComliteLoading && list.map((item:any) => {
            return (
              <div onClick={()=>{onClickAutoComlite(item.locale_names)}}> 
                {item.locale_names + ', г.Одесса'} 
              </div>
            )
          })}

        </div>
      </div> }
      
      <span>и получайте уведомления о доступности коменальных услуг заблаговременно</span>

      <button type="button" onClick={clickHandler}>Далее </button>
    </main>
  );
}

export default Auth;
